.example {
    position: relative;
    padding: 0;
    width: 370px;
    display: block;
    overflow: hidden;
  }
  .example img{
      width: 370px;
      height: 290px;
      overflow: hidden;
  }
  .content h5{
    margin-top: -35px;
      padding-bottom: 10px;
      font-size: 25px;
      text-transform: uppercase;
      letter-spacing: 1px;
      font-family: 'Open Sans', sans-serif;
      color: white;
  }
  .content p{
    padding-left: 5px;
    padding-right: 5px;
      font-size: 16.5px;
      letter-spacing: 0.5px;
      font-family: 'Open Sans', sans-serif;
      color: white;
  }
  .content {
    opacity: 0;
    font-size: 40px;
    position: absolute;
    top: 0;
    left: 0;
    color: #1c87c9;
    background-color: rgba(187, 68, 68, 0.5);
    width: 370px;
    height: 290px;
    transition: all 500ms ease-out;
    text-align: center;
  }
  .example .content:hover {
    opacity: 1;
  }
  .example .content .text {
    opacity: 1;
    transition-delay: 0s;
    transition-duration: 0.4s;
  }
  .example .content:hover .text {
    opacity: 1;
    transform: translateY(250px);
    -webkit-transform: translateY(70px);
  }

